.load {
  background: linear-gradient(45deg, #343a40, #6c757d, #343a40, #f8f9fa);
  background-position-x: 616%;
  background-size: 200% 200%;
  animation: load 0.2s ease 0s infinite alternate;
}

@keyframes load {
  from {
    background-position-x: -100%;
  }
  to {
    background-position-x: 0%;
  }
}
